* {
  font-family: Karla, Arial, Helvetica, sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

#root, body {
  position: relative;
  background-color:rgb(194, 189, 189);
  height: 100vh;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  overflow: hidden;
}